/* Medium Devices, Desktops */
/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .background .background__skills img {
    max-width: 50px;
    margin-top: 20px;
    display: block;
  }
  .background .background__skill-avatars {
    float: none;
  }
  .background .background__skill-avatars-container {
    float: none;
    margin-top: 26px;
  }
  .background .background__skill-number {
    line-height: 54px;
  }
  .following .following__logo {
    margin-right: 20px;
    text-align: center;
  }
  .profile__stats__divider {
    border-right: none;
  }
  .logo {
    display: none;
  }
  .logo-mobile {
    display: block;
    float: left;
  }
  .logo-mobile img {
    width: 30px;
    padding-top: 23px;
    padding-left: 20px;
    padding: 23px 20px 20px 20px;
  }
  .navigation {
    display: none;
  }
  .mobile-navigation-container {
    display: block;
  }
  .navigation__notifications {
    display: none;
  }
  .navigation__user-info {
    padding-left: 0;
  }
  .navigation__user-info li {
    display: block;
    padding: 0 20px 15px 0;
  }
  .navigation__user-info li.active {
    border-bottom: none;
  }
  .navigation__main-links {
    border-left: none;
    float: none;
    height: auto;
  }
  .navigation__main-links li {
    display: block;
    padding: 10px 0;
  }
  .navigation__top {
    height: auto;
  }
  .navigation__search {
    display: block;
  }
  .navigation__search input {
    float: none;
    margin-left: 0;
  }
  .navigation__main-links {
    float: none;
  }
  .navigation__account {
    display: none;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .background .background__experience__record {
    margin-bottom: 6px;
  }
  .background .background__experience__logo {
    float: none;
  }
  .background .background__sub-container {
    padding-left: 0;
  }
  .posts .posts__carousel:after {
    content: '';
  }
  .posts .carousel-cell {
    width: auto;
    height: auto;
    margin-right: 0;
    margin-bottom: 40px;
  }
  .recommendations .recommendations__carousel:after {
    content: '';
  }
  .recommendations .carousel-cell {
    width: auto;
    height: auto;
    margin-right: 0;
    margin-bottom: 40px;
  }
}

